/* Colores  */
/* fonts */
/* responsive */
html {
  box-sizing: border-box;
  font-size: 62.5%;
  width: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "IBM Plex Mono", monospace;
  line-height: 2;
  font-size: 1.6rem;
  background-color: #344C36;
}

h1, h2, h3, h4 {
  font-family: "Germania One", system-ui;
  line-height: 1.2;
  margin: 0;
}

h1 {
  font-size: 4.8rem;
  color: #FFF;
}

h2 {
  font-size: 4rem;
}

h3 {
  font-size: 3.2rem;
}

h4 {
  font-size: 2.8rem;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: #FFF;
  font-size: 2.4rem;
}

.header {
  display: flex;
  flex-direction: column;
}

.logo {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 2rem;
}
.logo img {
  width: 7rem;
  height: 7rem;
}

.navegador {
  display: flex;
  flex-direction: column;
  justify-content: right;
  align-items: center;
  gap: 0;
}
.navegador a {
  padding: 1rem;
}
.navegador a:hover {
  color: #FEAD17;
  transition: 500ms ease-in-out;
}
.navegador .contactameBtn {
  background-color: #FEAD17;
  border-radius: 15px;
  padding: 0.2rem 1rem;
  color: #000;
}
.navegador .contactameBtn:hover {
  color: #FFF;
}

@media (min-width: 900px) {
  .header {
    flex-direction: row;
    justify-content: space-between;
  }
  .logo {
    justify-content: start;
  }
  .navegador {
    flex-direction: row;
    gap: 2rem;
  }
}